.preloader-section {
  min-height: 50vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.preloader {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}

.preloader__segment {
  animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  transform-origin: 40px 40px;
}

.preloader__segment:after {
  content: " ";
  display: block;
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: #2BE080;
  margin: -4px 0 0 -4px;
}

.preloader__segment:nth-child(1) {
  animation-delay: -0.036s;
}

.preloader__segment:nth-child(1):after {
  top: 63px;
  left: 63px;
}

.preloader__segment:nth-child(2) {
  animation-delay: -0.072s;
}

.preloader__segment:nth-child(2):after {
  top: 68px;
  left: 56px;
}

.preloader__segment:nth-child(3) {
  animation-delay: -0.108s;
}

.preloader__segment:nth-child(3):after {
  top: 71px;
  left: 48px;
}

.preloader__segment:nth-child(4) {
  animation-delay: -0.144s;
}

.preloader__segment:nth-child(4):after {
  top: 72px;
  left: 40px;
}

.preloader__segment:nth-child(5) {
  animation-delay: -0.18s;
}

.preloader__segment:nth-child(5):after {
  top: 71px;
  left: 32px;
}

.preloader__segment:nth-child(6) {
  animation-delay: -0.216s;
}

.preloader__segment:nth-child(6):after {
  top: 68px;
  left: 24px;
}

.preloader__segment:nth-child(7) {
  animation-delay: -0.252s;
}

.preloader__segment:nth-child(7):after {
  top: 63px;
  left: 17px;
}

.preloader__segment:nth-child(8) {
  animation-delay: -0.288s;
}

.preloader__segment:nth-child(8):after {
  top: 56px;
  left: 12px;
}

@keyframes lds-roller {

  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }

}
